import { Card, CardContent } from "@/components/ui/card";
import { Carousel, CarouselContent, CarouselItem, CarouselNext, CarouselPrevious } from "@/components/ui/carousel";




export default function Projects(){
    return (
         <div className="px-40 py-24">
        <Carousel >
          <CarouselPrevious></CarouselPrevious>
          <CarouselContent className="p-2 bg-sky-50">
            {Array.from({ length: 5 }).map((_, index) => (
              <CarouselItem key={index} >
                <div className="flex gap-8">
                  <img className="w-full h-128 rounded-lg" src={"smu best group buddies - Copy.jpg"}></img>
                  <Card>
                    <CardContent className="items-start justify-left p">
                      <p className="text-2xl font-semibold">{index + 1}</p>
                      <p>asdfasdfasdfssdfsadfasdfasdfasdfasdf</p>
                    </CardContent>
                  </Card>

                </div>
              </CarouselItem>
            ))}
          </CarouselContent>
          <CarouselNext></CarouselNext>
        </Carousel>
      </div>
        
    );
}